.author .box {
	border-radius: $radius;
	> div:first-child {
		display: flex;		
		> div:first-child {
			margin-right: 1rem;
		}
	}
}

.author .box img:first-child {
	border-radius: 50%;
	max-width: 75px;
}

.author .box img[src*='.svg'] {
	max-width: 30px;
	display: inline-block;
}

